import React, { Component } from "react";
import "../../../Style/home.css";

const Pagination = (props) => {
  const pageLinks = [];
  for (let i = 1; i <= props.pages + 1; i++) {
    let active = "";

    if (props.currentPage == i) {
      active = "active";
    } else {
      active = "notactive";
    }

    // let active = props.currentpage - 1 == i ?  : "notactive";

    pageLinks.push(
      <span
        className={"spread "}
        key={i}
        onClick={() => {
          props.nextPage(i);
        }}
      >
        <a className={active} style={{ cursor: "pointer" }}>
          {i}
        </a>
      </span>
    );

    if (i == 10) {
      break;
    }
  }

  /*

  <button href="" className={"ui secondary button " + active}>
          {i}
        </button>
  */

  return (
    <div
      className="container"
      style={{ paddingTop: "30px", paddingBottom: "30px" }}
    >
      <div className="row">
        <ul className="ui massive  horizontal list ">
          {props.currentPage > 1 ? (
            <span
              className={"spread"}
              onClick={() => {
                props.nextPage(props.currentPage - 1);
              }}
            >
              <a style={{ color: "white", cursor: "pointer" }}>Prev</a>
            </span>
          ) : (
            <span className={"spread"}>
              <a style={{ color: "white", cursor: "pointer" }}>Prev</a>
            </span>
          )}

          {pageLinks}

          {props.currentPage < props.pages + 2 ? (
            props.currentPage < props.pages + 1 && props.currentPage < 10 ? (
              <span
                className={"spread"}
                onClick={() => {
                  props.nextPage(props.currentPage + 1);
                }}
              >
                <a style={{ color: "white", cursor: "pointer" }}>Next</a>
              </span>
            ) : (
              <span className={"spread"}>
                <a style={{ color: "white", cursor: "pointer" }}>Next</a>
              </span>
            )
          ) : (
            ""
          )}
        </ul>
      </div>
    </div>
  );
};

export default Pagination;
